<template>
	<div>
		<div class="first_screen">
			<div class="wrapper_full">
			  <div class="hello_block">
				<div class="overlay"></div>
				<div class="hello_block__name">
				  Привет, меня зовут Сергей Лисин и я
				</div>
				<div class="hello_block__work">
				  FULL STACK WEB Разрабочик
				</div>
				<div class="hello_block__desc">
				  создаю современные и адаптивные web-приложения
				</div>
			  </div>
			</div>
		</div>
		  <div class="my_skills_block" id="home">
			<div class="wrapper">
			  <div class="my_skills">
				<div class="h2">Мои услуги</div>
				<div class="line"></div>
				<div class="description">Я предлагаю вам различные услуги, чтобы помочь вам решить разные проблемы, которые могут возникнуть на сайте. Вот их перечень</div>

				<div class="skills">
				  <div class="skills_item">
					<div class="skills_item__image">
					  <i class="fas fa-tachometer-alt"></i>
					</div>
					<div class="skills_item__title">
					  Оптимизация
					</div>
					<div class="skills_item__description">
					  Провожу оптимизацию скорости загрузки вашего сайта
					</div>
				  </div>
				  <div class="skills_item">
					<div class="skills_item__image">
					  <i class="fas fa-puzzle-piece rotate_my"></i>
					</div>
					<div class="skills_item__title">
					  Создание сайтов
					</div>
					<div class="skills_item__description">
					  Создаю сайты разной сложности
					</div>
				  </div>
				  <div class="skills_item">
					<div class="skills_item__image">
					  <i class="fas fa-award"></i>
					</div>
					<div class="skills_item__title">
					  Доработка сайта
					</div>
					<div class="skills_item__description">
					  Исправляю неисправности на вашем сайте
					</div>
				  </div>
				  <div class="skills_item">
					<div class="skills_item__image">
					  <i class="fas fa-question-circle"></i>
					</div>
					<div class="skills_item__title">
					  Консультации
					</div>
					<div class="skills_item__description">
					  Я могу помочь вам по какому-либо вопросу, касающемуся моей сферы
					</div>
				  </div>
				</div>
			  </div>
			</div>
		  </div>
		  <div class="reasons_work_block">
		<div class="wrapper">
			<div class="reasons_work">
				<div class="h2">почему выбрать меня?</div>
				<div class="line"></div>
				<div class="description">Если вы закажите сайт у меня, то это обойдется в несколько раз дешевле, чем заказ у студии разработки сайтов.</div>

				<div class="reasons">
					<div class="reasons_item">
						<div class="reasons_item__image">
							<i class="far fa-clock"></i>
						</div>
						<div class="reasons_item__title">Скорость не в ущерб качеству</div>
						<div class="reasons_item__description">
							<ul>
								<li>
									<i class="far fa-check-square"></i>
									Для реализации работы используются инструменты, которые позволяют повысть не только скорость разработки, но и ее качество
								</li>
								
							</ul>
						</div>
					</div>
					<div class="reasons_item">
						<div class="reasons_item__image">
							<i class="fas fa-hourglass-start"></i>
						</div>
						<div class="reasons_item__title">Система неустоек</div>
						<div class="reasons_item__description">
							<ul>
								<li>
									<i class="far fa-check-square"></i>
									За каждый просроченный день возвращаю 20% стоимости работы
								</li>
							</ul>
						</div>
					</div>
					<div class="reasons_item">
						<div class="reasons_item__image">
							<i class="fas fa-check-circle"></i>
						</div>
						<div class="reasons_item__title">Контроль развития проекта</div>
						<div class="reasons_item__description">
							<ul>
								<li>
									<i class="far fa-check-square"></i>
									Вместе с вами определяем задачи проекта и заносим их в сервис Trello
								</li>
								<li>
									<i class="far fa-check-square"></i>
									Перед окончанием работы над каждой частью вы проверяете проект и если что-то не так, то проводятся изменения
								</li>
								<li>
									<i class="far fa-check-square"></i>
									Быстрая связь в оговоренный промежуток времени
								</li>
							</ul>
						</div>
					</div>
					<div class="reasons_item">
						<div class="reasons_item__image">
							<i class="fas fa-money-bill-alt"></i>
						</div>
						<div class="reasons_item__title">Вы платите за результат</div>
						<div class="reasons_item__description">
							<ul>
								<li>
									<i class="far fa-check-square"></i>
									Итоговая сумма разделяется на некоторое количество частей
								</li>
								<li>
									<i class="far fa-check-square"></i>Оплата производится только за выполненную часть работы
								</li>
							</ul>
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
	<div class="my_variants_block" id="works">
		<div class="wrapper">
			<div class="h2">ценовая политика</div>
			<div class="line"></div>
			<div class="description">
			Данная ценовая политика рапространяется как на создание сайта из макета psd, так и на копирование уже существующего сервиса</div>
			<div class="my_variants">
				<ul class="menu_tabs">
					<li v-for="(category, index) in categories"
						:class="active_tab == index ? 'show_tab' : ''"
						@click="changeTabs(index, category.sys_name)">{{ category.name }}</li>
				</ul>
				<div class="content_tabs">
					<div v-for="(itemCategoryData, index) in itemsByCategory"
						 class="content_tab_one" 
						 v-show="active_tab == index"
						 :key="itemCategoryData.category_data.sys_name">
						<div class="content_tab_hidden">
							<div class="block properties">
								<div class="block__h1 first_h1">описание</div>
								<ul class="block__desc left_text">
									<li v-for="(property, index) in itemCategoryData.category_data.list_rate[0].properties" :key="index">{{ property.name }}</li>
									<li>Цена</li>
								</ul>
							</div>
							<div v-for="(type, index) in itemCategoryData.category_data.list_rate"  :key="type.price"
							     :class="['block', hidden_offer == index ? 'active_block_price' : '' ]">
								<div class="block__h1">
									<div class="block__h1--btn_prev" @click="prevOffer()">
										<i class="fas fa-chevron-left"></i>
									</div>
									<div class="block__h1--title">{{ type.name }}</div>
									<div class="block__h1--btn_next" @click="nextOffer()">
										<i class="fas fa-chevron-right"></i>
									</div>
								</div>
								<ul class="block__desc">
									<li v-for="(type_property, index) in type.properties" :key="index">
										<template v-if="typeof(type_property.value) == 'string'">
											{{ type_property.value }}
										</template>
										<template v-if="typeof(type_property.value) == 'boolean'">
											<i v-if="type_property.value"
											   class="fas fa-check check">    	
										    </i>
										    <i v-else
									    	   class="fas fa-times cross">
											 </i>										
										</template>
									</li>
									<li>{{ type.price }} руб</li>
								</ul>
							</div>
						</div>
						<div class="content_tab_show">
							<div class="block">
								<div class="block__h1">описание</div>
								<ul class="block__desc left_text">
									<li v-for="(property, index) in itemCategoryData.category_data.list_rate[0].properties" :key="index">{{ property.name }}</li>
									<li>Цена</li>
								</ul>
							</div>
							<div v-for="(type, index) in itemCategoryData.category_data.list_rate"  :key="index"
								 class="block">
								<div class="block__h1">{{ type.name }}</div>
								<ul class="block__desc">
									<li v-for="(type_property, index) in type.properties" :key="index">
										<template v-if="typeof(type_property.value) == 'string'">
											{{ type_property.value }}
										</template>
										<template v-if="typeof(type_property.value) == 'boolean'">
											<i v-if="type_property.value"
											   class="fas fa-check check">    	
										    </i>
										    <i v-else
									    	   class="fas fa-times cross">
											 </i>					
										</template>
									</li>
									<li>{{ type.price }} руб</li>
								</ul>
							</div>
						</div>
						<div class="examples">
							<div class="examples__h1">примеры</div>
							<div class="examples__block works">
								<div class="grid" v-for="(item, index) in itemCategoryData.items">
									<figure class="effect-bubba">
										<img :src="item.demo" alt="img02"/>
										<figcaption>
											<h2 v-html="item.title"></h2>
											<p>Подробности проекта</p>
											<router-link :to="'/work/' + item.short_title">
												<a>Подробнее</a>
											</router-link>
										</figcaption>		
									</figure>
								</div>
							</div>
						</div>
					</div>
					
				</div>
				
			</div>
		</div>
	</div>
	</div>
</template>
<script>
	import {mapGetters} from 'vuex';
	export default{
		data(){
			return {
				active_tab: 0,
				hidden_offer: 0
			}
		},
		computed: {
			...mapGetters([
				'works',
				'itemsByCategory',
				'categories'
				]),
			
		},
		methods: {
			changeTabs(category_name){
				this.active_tab = category_name;
			},
			prevOffer(){
				this.hidden_offer -= 1;
				if(this.hidden_offer < 0){
					this.hidden_offer = 2;
				}
				console.log(this.hidden_offer);
			},
			nextOffer(){
				this.hidden_offer += 1;
				if(this.hidden_offer >= 3){
					this.hidden_offer = 0;
				}
				console.log(this.hidden_offer);
			}
		}
	}
</script>
<style scoped="">
	.slide-fade-enter-active {
	transition: all .3s ease;
	}
	.slide-fade-leave-active {
	transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
	}
	.slide-fade-enter, .slide-fade-leave-to
	/* .slide-fade-leave-active до версии 2.1.8 */ {
	transform: translateX(10px);
	opacity: 0;
	}
</style>


