<template>
  <div id="app">
  	<transition name="slide" mode="out-in">
		<router-view></router-view>
	</transition>
  <div class="lets_begin_block">
	<div class="wrapper">
	  <div class="lets_begin">
		<div class="h2 h2_lets_begin">Вы готовы со мной работать?</div>
		<div class="description description_lets_begin">Я доступен для проектной работы в качестве фрилансера</div>
		<div class="btn btn-white">Свяжитесь со мной в соц сетях</div>
	  </div>
	</div>
  </div>
  <footer>
	<div class="wrapper">
	  <div class="footer">
		<div class="btns_social">
		  <div class="btn_social">
			<a href="https://vk.com/liswebdev" target="_blank"><i class="fab fa-vk"></i></a>
		  </div>
		  <div class="btn_social">
			<a href="https://tlgg.ru/@LisDev" target="_blank"><i class="fab fa-telegram-plane"></i></a>
		  </div>
		</div>
		<div class="copy">
		  &copy; 2020 Лисин Сергей
		</div>
	  </div>
	</div>
  </footer>

  </div>
</template>

<script>
	import './assets/test.js';
	export default {
		
	}
</script>
<style scoped>
	.slide-enter-active{
		animation: slideIn 1s;
	}
	.slide-leave-active{
		animation: slideOut 1s;
	}
	@keyframes slideIn{
		from{
			transform: translateX(-100vw);
		}
		to{
			transform: translateX(0vw);
		}
	}
	@keyframes slideOut{
		from{
			transform: translateX(0vw);
		}
		to{
			transform: translateX(100vw);
		}
	}
</style>


