<template>
	<div>
		<header>
			<div class="wrapper">
				<div class="header">
					<router-link :to="{name: 'main'}">
						<div class="logo"><a href="index.html">Лисин Сергей</a></div>
					</router-link>
				</div>
			</div>
		</header>
		<section class="my_works_block" id="works">
			<div class="wrapper">
				<div class="one_work">
					<div class="one_work__description">
						<div class="one_work__description--h2" v-html="dataPage.title"></div>
						<div v-html="dataPage.desc"></div>
					</div>
					<div class="one_work__slider">
						<div class="my-gallery">
							<figure v-for="(image, index) in dataPage.images" :key="index">
								<a>
								<img @click="showPhotoSwipe(index)" :src="image.src" alt="">
								</a>
							</figure>
						</div>
					</div>
					<v-photoswipe :isOpen="isOpen" :items="dataPage.images" :options="options" @close="hidePhotoSwipe"></v-photoswipe>
				</div>
			</div>
		</section>
	</div>
</template>
<script>
	import { PhotoSwipe, PhotoSwipeGallery } from 'v-photoswipe'

	import {mapGetters} from 'vuex';
	export default {
		components: {
			'v-photoswipe': PhotoSwipe,
			'v-photoswipe-gallery': PhotoSwipeGallery
		},
		computed: {
			...mapGetters([
				'getItems'
				]),
			short_title(){
				return this.$route.params.name;
			},
			dataPage(){
				return this.$store.getters['getItem'](this.short_title);
			},
		},
		data () {
			return {
			  isOpen: false,
			  isOpenGallery: false,
			  options: {
				index: 0,
			  },
			  optionsGallery: {},
			  
			}
		},
	methods: {
		showPhotoSwipe (index) {
			this.isOpen = true
			this.$set(this.options, 'index', index)
		},
		hidePhotoSwipe () {
			this.isOpen = false
			}
		},
		
	}
</script>



