import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

import OneWork from './components/OneWork.vue';
import AllWorks from './components/AllWorks.vue';

const routes = [
	{
		name: 'main',
		path: '/',
		component: AllWorks,
		beforeEnter(from, to, next){
			window.scrollTo(0, 0);
			next();
		}
	},
	{
		path: '/work/:name',
		component: OneWork,
		beforeEnter(from, to, next){
			window.scrollTo(0, 0);
			next();
		}
	}
];

export const router = new VueRouter({
	routes,
	mode: 'history'
});


